.Notifications{
   width: 100%;
   height: 45px;
   box-shadow: 0 0 1px 1px var(--gray-color);
   padding: 5px;
   border-radius: 10px;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   flex-direction: row;
   gap: 10px;
}
.Notifications>span{
    font-size: 12px;
}